@import url(https://fonts.googleapis.com/css2?family=Overpass:wght@100;400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 15px;
  font-family: 'Overpass', sans-serif;
}

body {
  min-width: 375px;
  min-height: 100vh;
  max-width: 1440px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  background-color: hsl(216, 12%, 8%);
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}


.container {
  display: flex;
  align-items: center;
  justify-content: center;
}


.flip {
    width: 22rem;
    height: 22.4rem;
    border-radius: 20px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}
.card {
    width: 100%;
    height: 100%;
    background-color: #212832;
    border-radius: 1.8rem;
    position: relative;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transition: all 1500ms;
}

.card-face,
.card-back {
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

.card-face {
    padding: 1.6rem; 
}

.boxstar {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.circle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.6rem;
    height: 2.6rem;
    background-color: #263039;
    border-radius: 50%;
    margin-bottom: 1.3rem;
    box-shadow: 1px 1px 3px 0px hsl(216, 12%, 8%);
}

.img {
    width: .9rem;
}

.title {
    color: #FDFEFE;
    margin-top: .5rem;
}

h1 {
    font-size: 1.5rem;
    font-weight: 700;
    text-align: left;
}

.text {
    color: #868D97;
    margin-bottom: 1.7rem;
    margin-top: .5rem;
    text-align: left;

}

.informe {
    font-size: .8rem;
    line-height: 1rem;
}



.buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1.4rem;
}

.rating {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: .9rem;
    width: 2.8rem;
    height: 2.8rem;
    color: #868D97;
    font-weight: 700;
    background-color: #263039;
    border-radius: 50%;
    border: none;
}

.rating:hover {
    cursor: pointer;
    background-color: hsl(216, 12%, 54%);
    color: white;
    transition: all .5s linear;
}

.rating:focus {
    background-color: #FC7613;
    color: white;
    outline: none;

}

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 1.2rem;
}

input {
    color: #FEEBBF;
    background-color: #FC7613;
    text-transform: uppercase;
    border: none;
    min-width: 97%;
    padding: .6rem;
    border-radius: 5rem;
    letter-spacing: .1rem;
    font-size: .85rem;
    font-weight: 800;
    outline: none;
    transition: all .5s linear;
}

input:disabled {
    pointer-events: none;
    background-color: #af9386;
}

input:hover {
    cursor: pointer;
    background-color: white;
    color: #FC7613;
}




.card-back {
    padding: 1.6rem; 
    -webkit-transform: rotateY(180deg); 
            transform: rotateY(180deg);
    display: flex;
    align-items: center;
    flex-direction: column;
}

.boximg {
    margin-top: .6rem;
}

.boximg:hover {
    cursor: pointer;
}

.imgthank {
    width: 9.4rem;
}

.boxrating {
    background-color: hsl(213, 19%, 18%);
    padding: .3rem 1rem;
    border-radius: 1rem;
    margin-top: .2rem;
}

.textrating {
    font-size: .80rem;
    color: hsl(25, 97%, 53%);
}

.acknowledgment {
    margin-top: 1.7rem;
}

.acknowledgment h1 {   
    color: white;
    font-size: 1.5rem;
}

.texttks {
    margin-top: .5rem;
    text-align: center;
}

.informe {
    color: hsl(217, 12%, 63%);
    font-size: .83rem;
    line-height: 1rem;
}

.hide {
    color: orange;
    font-size: .6rem;  
    text-align: center;
    transition: all .5s linear;
    opacity: 0;
}
.footerBox {
    margin-top: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: .5rem;
    gap: .5rem;
}

.footerAvatar {
    width: 2.5rem;
    margin-left: 1rem;
}

.author {
    display: flex;
    align-items: center;
    grid-gap: .8rem;
    gap: .8rem;    
}

.authorText {
    color: #02B8AC;
    font-size: .84rem;
}

.btnFlip {
    opacity: 1;
	outline: 0;
	color: #02B8AC;
	line-height: 25px;
	position: relative;
	text-align: center;
	letter-spacing: 1px;
	display: inline-block;
	text-decoration: none;
	text-transform: uppercase;
    font-weight: 800;
}


.btnFlip:hover::after {
    opacity: 1;
	-webkit-transform: translateY(0) rotateX(0);
	        transform: translateY(0) rotateX(0);
}

.btnFlip:hover::before {
    opacity: 0;
	-webkit-transform: translateY(50%) rotateX(90deg);
	        transform: translateY(50%) rotateX(90deg);
}



.btnFlip::after {
    top: 0;
		left: 0;
		opacity: 0;
		width: 100%;
		color: #323237;
		display: block;
		transition: 0.5s;
		position: absolute;
		background: #adadaf;
		content: attr(data-back);
		-webkit-transform: translateY(-50%) rotateX(90deg);
		        transform: translateY(-50%) rotateX(90deg);
        font-size: .6rem;
}

.btnFlip::before {
    top: 0;
		left: 0;
		opacity: 1;
		color: hsl(216, 12%, 8%);
		display: block;
		padding: 0 0;
		line-height: 25px;
		transition: 0.5s;
		position: relative;
		background: #FEEBBF;
		content: attr(data-front);
		-webkit-transform: translateY(0) rotateX(0);
		        transform: translateY(0) rotateX(0);
        font-size: .6rem;
}
    
	
	
	


